@import "styles/variables";
@import "styles/mixins";

.section {
  padding: 4.7rem 0;

  :global {
    .swiper-slide {
      width: 100%;

      &:not(:last-of-type) {
        margin-right: 2rem;
      }
    }
  }
}

.slider {
  position: relative;
  margin-top: 3.3rem;
}

.cardContent {
  margin: 0;
  padding: 0;
}

.frame {
  position: relative;
  height: 27.7rem;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.3rem;
    background-color: var(--color-highlight-mosaic);
    z-index: 1;
  }
}

.iconDots {
  display: none;
}

.photo {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  margin-top: 2.3rem;
}

.name {
  @include h2-adaptiveSpecial(var(--color-font-main));

  margin: 0;
}

.expertise {
  @include regular(var(--color-font-main));

  margin: 0;
  opacity: 0.7;
}

.address {
  @include regularTabletScaling(var(--color-font-main));

  margin: 0.8rem 0 0 0;
}

.quote {
  display: none;
}

.btnAll {
  margin-top: 1.4rem;
}

.controls {
  position: absolute;
  top: -5.5rem;
  right: 0;
  display: flex;
  align-items: flex-start;

  .btnNext {
    margin-left: 3rem;
  }
}

@media (min-width: $widthTabletMin) {
  .section {
    padding: 5rem 0 4rem 0;

    :global {
      .swiper-container {
        margin-left: -2.5rem;
        padding-right: 2.5rem;
        padding-left: 2.5rem;
        width: calc(100% + 5rem);
      }

      .swiper-slide {
        width: 64.6rem;

        &:not(:last-of-type) {
          margin-right: 5rem;
        }

        &:last-of-type {
          margin-right: 5rem;
        }
      }
    }
  }

  .slider {
    margin-top: 2.5rem;
  }

  .cardContent {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
  }

  .frame {
    order: 2;
    width: 29.2rem;
    height: 36.1rem;
  }

  .info {
    order: 1;
    margin-top: 0;
    padding-right: 3.2rem;
    width: calc(100% - 29.2rem);
  }

  .address {
    margin: 0.9rem 0 0 0;
  }

  .quote {
    @include h5-italic(var(--color-font-main));

    position: relative;
    display: block;
    margin: 3rem 0 0 0;
    padding-right: 2rem;

    span {
      opacity: 0.7;
    }
  }

  .iconQuote {
    position : absolute;
    top: -0.3rem;
    right: -3.2rem;
    width: 3.2rem;
    fill: var(--color-font-main);
    opacity: 0.5;
  }

  .btnAll {
    margin-top: 4.4rem;
    width: 25.5rem;
  }

  .controls {
    display: none;
  }
}

@media (min-width: $widthDesktopMin) {
  .section {
    :global {
      .swiper-container {
        margin-left: 0;
        padding-right: 0;
        padding-left: 0;
        width: 100%;
        overflow: visible;
      }

      .swiper-slide {
        width: 100%;
        margin-right: 0;
      }
    }
  }

  .slider {
    margin-top: 5rem;
  }

  .slide {
    display: flex;
    justify-content: space-between;
    background-color: $colorMainBg;
  }

  .tabContent {
    width: 87.7rem;
  }

  .frame {
    position: relative;
    width: 47.6rem;
    height: 47.6rem;
    overflow: hidden;
  }

  .iconDots {
    position: absolute;
    top: 11.2rem;
    left: 3.2rem;
    display: block;
    width: 47.6rem;
    opacity: 0.3;
    transform-origin: center;
    transform: rotate(90deg);
  }

  .photo {
    position: relative;
  }

  .info {
    width: calc(100% - 47.6rem);
  }

  .expertise {
    margin-top: 1.4rem;
  }

  .address {
    margin: 1.4rem 0 0 0;
  }

  .quote {
    padding: 0;
  }

  .iconQuote {
    top: 0;
    right: auto;
    left: calc(-3.2rem - 2.1rem);
  }

  .tabs {
    @include resetList
  }

  .tab {
    &:not(:first-of-type) {
      margin-top: 1rem;
    }
  }

  .tabSwitch {
    @include resetButton;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2.7rem 4.3rem 2.5rem 4.3rem;
    width: 43.7rem;
    border: 1px solid var(--color-highlight-darker);
    cursor: pointer;
    transition-duration: 0.3s;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: -0.3rem;
      width: 0.3rem;
      height: 100%;
      background-color: var(--color-highlight-mosaic);
      opacity: 0;
      pointer-events: none;
      transition-duration: 0.3s;
    }

    &:hover {
     border-color: var(--color-highlight-buttons);
    }
  }

  .tabSwitchSelected {
    pointer-events: none;
    box-shadow: 0px 0.8rem 2rem rgba(0, 0, 0, 0.08);
    border-color: var(--color-highlight-darker);

    &::before {
      opacity: 1;
      pointer-events: all;
    }
  }

  .btnName {
    @include h5(var(--color-font-main));

    text-align: left;
  }

  .btnExpertise {
    @include regular(var(--color-font-main));

    opacity: 0.7;
  }

  .controls {
    top: calc(-3rem - 6.3rem);
    display: flex;
  }
}
